import React from 'react';
import {Element} from 'react-scroll';

export default function () {
  const cardNameEnglish = <span> <br/><br/></span>;
  const cardNameFrench = <span> <br/><br/></span>;
  const plans = {
    english: [
      {
        name: cardNameEnglish,
        for: '',
        lessonsCount: '8 ',
        price: 4000,
      },
      {
        name: cardNameEnglish,
        for: ' ',
        lessonsCount: '8 ',
        price: 4800,
      },
      {
        name: cardNameEnglish,
        for: ' ',
        lessonsCount: '8 ',
        price: 4800,
      },
      {
        name: cardNameEnglish,
        for: ' ',
        lessonsCount: '8 ',
        price: 5200,
      },
      {
        name: <span><br/> <br/></span>,
        for: '',
        lessonsCount: '8 ',
        price: 6400,
      },
      {
        name: <span><br/> <br/></span>,
        for: '',
        lessonsCount: '8 ',
        price: 6400,
      },
      {
        name: <span> <br/><br/></span>,
        for: '',
        lessonsCount: '4 ',
        price: 2400,
      },
    ],
    french: [
      {
        name: cardNameFrench,
        for: '',
        lessonsCount: '8 ',
        price: 4800,
      },
      {
        name: cardNameFrench,
        for: ' ',
        lessonsCount: '8 ',
        price: 5600,
      },
      {
        name: cardNameFrench,
        for: ' ',
        lessonsCount: '8 ',
        price: 6000,
      },
      {
        name: cardNameFrench,
        for: '',
        lessonsCount: '8 ',
        price: 6400,
      },
    ],
    italian: [
      {
        name: <span> <br/><br/></span>,
        for: '',
        lessonsCount: '8 ',
        price: 6400,
      },
    ],
  };

  return (
    <Element name="plans" id={"plans"} className="container">
      <div className="row">
        <div className="col py-3">
          <h2 className="section-heading">
             
          </h2>
          {/*<hr/>*/}
        </div>
      </div>

      <div className="row justify-content-center">
        <div className="col-12 py-3">
          <h5 className="text-center"> </h5>
        </div>
        {plans.english.map(getTemplate)}
      </div>

      <div className="row justify-content-center">
        <div className="col-12 py-3">
          <h5 className="text-center"> </h5>
        </div>
        {plans.french.map(getTemplate)}
      </div>

      <div className="row justify-content-center">
        <div className="col-12 py-3">
          <h5 className="text-center"> </h5>
        </div>
        {plans.italian.map(getTemplate)}
      </div>

    </Element>
  );
}

function getTemplate(card, key) {
  return (
    <div className="plan col-12 col-sm-6 col-lg-4 col-xl-3 p-3" key={key}>
      <div className="card">
        <div className="text-center card-body">
          <h5 className="plan__title card-title">
            {card.name}
            <mark>{card.for}</mark>
          </h5>
          <p className="mb-2">
            {card.lessonsCount}
          </p>
          {/*<p className="mb-2">*/}
            {/*<small><s>{card.price} </s> <span className="badge badge-primary text-white">-25%</span></small>*/}
          {/*</p>*/}
          <p className="card-text">
            <span className="lead">{card.price} </span>
            <br/>
            <small>*  </small>
          </p>
          {/*<a href="#" className="btn btn-primary">Go somewhere</a>*/}
        </div>
      </div>
    </div>
  );
}



